dropdown-component {
  display: inline-block;
}

dropdown-component:hover > dropdown-trigger::before,
dropdown-trigger[aria-expanded=true]::before {
  content: "";
  position: absolute;
  left: -10px;
  top: 50%;
  width: calc(100% + 20px);
  height: 30px;
  transform-origin: top center;
  transform: perspective(50px) rotateX(50deg);
  z-index: 10;
}

dropdown-trigger {
  position: relative;
  cursor: pointer;
  user-select: none;
}

dropdown-panel {
  position: absolute;
  top: 100%;
  left: 0;
  opacity: 0;
  pointer-events: none;
}

dropdown-panel[wide] {
  width: 100%;
}

dropdown-component:hover > dropdown-panel,
dropdown-panel[aria-hidden=false] {
  opacity: 1;
  pointer-events: auto;
  visibility: visible;
}